import React from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import anime from 'animejs';

import { Link } from '../Link';

class Component extends React.Component {
  static displayName = 'Brand';

  static propTypes = {
    theme: PropTypes.object.isRequired,
    classes: PropTypes.object.isRequired,
    energy: PropTypes.object.isRequired,
    audio: PropTypes.object.isRequired,
    sounds: PropTypes.object.isRequired,
    className: PropTypes.any,
    link: PropTypes.string,
    hover: PropTypes.bool,
    stableTime: PropTypes.bool,
    onEnter: PropTypes.func,
    onExit: PropTypes.func,
    onLinkStart: PropTypes.func,
    onLinkEnd: PropTypes.func
  };

  static defaultProps = {
    link: '/'
  };

  constructor () {
    super(...arguments);

    const { energy, stableTime } = this.props;

    if (!stableTime) {
      energy.updateDuration({ enter: 820 });
    }
  }

  componentWillUnmount () {
    const paths = this.svgElement.querySelectorAll('path');
    anime.remove(paths);
  }

  enter () {
    const { energy, sounds, stableTime, onEnter } = this.props;
    const paths = this.svgElement.querySelectorAll('path');

    anime.set(this.svgElement, { opacity: 1 });

    sounds.logo.play();

    anime({
      targets: paths,
      strokeDashoffset: [anime.setDashoffset, 0],
      easing: 'linear',
      delay: (path, index) => stableTime ? 0 : index * energy.duration.stagger,
      duration: path => stableTime ? energy.duration.enter : path.getTotalLength(),
      complete: () => {
        onEnter && onEnter();
      }
    });
  }

  exit () {
    const { energy, sounds, onExit } = this.props;
    const paths = this.svgElement.querySelectorAll('path');

    sounds.fade.play();

    anime({
      targets: this.svgElement,
      easing: 'easeInCubic',
      duration: energy.duration.exit,
      opacity: 0
    });
    anime({
      targets: paths,
      strokeDashoffset: [anime.setDashoffset, 0],
      easing: 'linear',
      direction: 'reverse',
      duration: energy.duration.exit,
      complete: () => {
        anime.set(this.svgElement, { opacity: 0 });
        onExit && onExit();
      }
    });
  }

  render () {
    const {
      theme,
      classes,
      energy,
      audio,
      sounds,
      className,
      link,
      hover,
      stableTime,
      onEnter,
      onExit,
      onLinkStart,
      onLinkEnd,
      ...etc
    } = this.props;

    return (
      <h1 className={cx(classes.root, hover && classes.hover, className)} {...etc}>
        <Link
          className={classes.link}
          href={link}
          title='Swifty logo'
          onLinkStart={onLinkStart}
          onLinkEnd={onLinkEnd}
        >
          <span className={classes.title}>Swifty</span>
          <svg
            ref={ref => (this.svgElement = ref)}
            className={classes.svg}
            viewBox='-50 0 400 70'
            xmlns='http://www.w3.org/2000/svg'
            onMouseEnter={() => sounds.hover.play()}
          >
            <path className={classes.path} d="M 0 63 L 4.9 49.3 L 31.9 49.3 L 32 37.9 L 8.9 37.9 A 12.993 12.993 0 0 1 5.58 37.509 Q 1.701 36.483 0.346 32.761 A 10.506 10.506 0 0 1 0 31.6 Q 0 30 0.05 28.2 Q 0.1 26.4 0.1 24.8 L 0 24.8 L 0 7.5 A 14.028 14.028 0 0 1 0.637 5.277 Q 1.488 3.069 3 1.9 A 7.647 7.647 0 0 1 5.668 0.599 Q 6.748 0.3 8.026 0.225 A 14.968 14.968 0 0 1 8.9 0.2 L 49.7 0.2 Q 48.669 3.225 47.779 5.706 A 239.843 239.843 0 0 1 47 7.85 A 367.42 367.42 0 0 0 45.941 10.754 Q 45.448 12.123 45.021 13.352 A 219.272 219.272 0 0 0 44.9 13.7 L 44.9 13.6 L 34.9 13.6 L 34.9 13.7 L 17.9 13.7 L 17.9 24.8 L 40.9 24.7 Q 43.813 24.7 45.796 25.754 A 7.04 7.04 0 0 1 46.8 26.4 A 6.821 6.821 0 0 1 48.555 28.477 Q 49.08 29.407 49.444 30.591 A 15.276 15.276 0 0 1 49.8 32 A 1.906 1.906 0 0 1 49.789 32.198 Q 49.777 32.317 49.75 32.45 A 2.842 2.842 0 0 0 49.716 32.659 Q 49.7 32.788 49.7 32.9 L 49.9 32.9 L 49.9 56.2 A 8.645 8.645 0 0 1 48.399 59.505 Q 46.102 62.5 41 62.5 L 28.1 62.7 L 28.1 62.8 L 0 63 Z" id="0" vector-effect="non-scaling-stroke"/>
            <path className={classes.path} d="M 102.3 0 L 102.4 49.3 L 117.8 49.3 L 117.8 46.9 L 117.9 46.9 L 117.8 0 L 134.6 0 L 134.8 62.4 L 134.9 62.7 L 134.8 62.7 L 134.6 62.5 L 52.9 62.4 L 52.9 0 L 69.8 0 L 70 49.3 L 85.1 49.3 L 85.1 0.3 L 85.4 0.3 L 85.4 0 L 102.3 0 Z" id="1" vector-effect="non-scaling-stroke"/>
            <path className={classes.path} d="M 154.6 0.3 L 154.5 62.7 L 137.7 62.7 L 137.7 0.3 L 154.6 0.3 Z" id="2" vector-effect="non-scaling-stroke"/>
            <path className={classes.path} d="M 157.4 62.7 L 157.4 0.3 L 206.9 0.3 L 206.9 13.8 L 174.4 13.7 L 174.4 25.1 L 202 25.1 L 202 38.2 L 174.4 38.2 L 174.5 82 L 157.5 88.1 L 157.4 62.7 Z" id="3" vector-effect="non-scaling-stroke"/>
            <path className={classes.path} d="M 242.3 54.8 L 242.4 54.8 L 242.5 82.3 L 225.5 88.4 L 225.4 63 L 225.4 13.3 L 209.2 13.3 L 209.2 0.2 L 251.8 0.5 L 251.8 13.3 L 242.3 13.3 L 242.3 54.8 Z" id="4" vector-effect="non-scaling-stroke"/>
            <path className={classes.path} d="M 287.3 62.7 L 270.5 62.7 L 270.5 31 L 254.3 0.4 L 271.2 0.4 L 278.9 15.1 L 286.7 0.4 L 303.6 0.4 L 287.4 31 L 287.3 62.7 Z" id="5" vector-effect="non-scaling-stroke"/>
          </svg>
        </Link>
      </h1>
    );
  }
}

export { Component };
